export var Children = {
    join: function (nodes, join) {
        if (Array.isArray(nodes)) {
            var length_1 = nodes.length;
            var result_1 = [];
            nodes.forEach(function (node, index) {
                result_1.push(node);
                if (index < length_1 - 1) {
                    result_1.push(join);
                }
            });
            return result_1;
        }
        else {
            return nodes;
        }
    }
};
