import { Color } from "../classes/Color";
var hexToTColor = function (rgb) {
    var color = new Color(rgb);
    return {
        main: color.toRGBString(),
        dark: color.darker().toRGBString(),
        light: color.lighter().toRGBString(),
    };
};
export var PRIMARY_COLOR = hexToTColor('#1a6da9');
export var FOOTER_COLOR = hexToTColor('#333333');
export var BACKGROUND_COLOR = hexToTColor('#F4F4F5');
